"use client"

import { useState, useEffect, memo } from "react"
import { Button } from "@/components/ui/button"
import { useAuth } from "@/lib/hooks/use-auth"
import { storage } from "@/lib/storage"
import { FaRobot, FaCommentDots, FaSave, FaSpinner, FaToggleOn, FaToggleOff } from "react-icons/fa"
import { Skeleton } from "@/components/ui/skeleton"

function BusinessToolsTabComponent() {
  const { user, mutate: mutateAuth } = useAuth()
  const [awayMessage, setAwayMessage] = useState("")
  const [awayMessageEnabled, setAwayMessageEnabled] = useState(false)
  const [aiAutomationEnabled, setAiAutomationEnabled] = useState(false)
  const [saving, setSaving] = useState(false)
  const [loading, setLoading] = useState(true)
  const [success, setSuccess] = useState("")

  useEffect(() => {
    if (user?.business) {
      // Load away message settings (you'll need to add this to your database schema)
      // For now, using localStorage as a placeholder
      const savedAwayMessage = localStorage.getItem(`away_message_${user.business.id}`)
      const savedAwayEnabled = localStorage.getItem(`away_enabled_${user.business.id}`)
      const savedAiEnabled = localStorage.getItem(`ai_enabled_${user.business.id}`)
      
      if (savedAwayMessage) setAwayMessage(savedAwayMessage)
      if (savedAwayEnabled === "true") setAwayMessageEnabled(true)
      if (savedAiEnabled === "true") setAiAutomationEnabled(true)
      
      setLoading(false)
    }
  }, [user])

  const handleSave = async () => {
    if (!user?.business) return

    setSaving(true)
    setSuccess("")

    try {
      // Save to localStorage (replace with actual database calls)
      localStorage.setItem(`away_message_${user.business.id}`, awayMessage)
      localStorage.setItem(`away_enabled_${user.business.id}`, awayMessageEnabled.toString())
      localStorage.setItem(`ai_enabled_${user.business.id}`, aiAutomationEnabled.toString())

      // TODO: Save to database when schema is ready
      // await storage.updateBusinessSettings(user.business.id, {
      //   awayMessage,
      //   awayMessageEnabled,
      //   aiAutomationEnabled,
      // })

      setSuccess("Settings saved successfully!")
      setTimeout(() => setSuccess(""), 3000)
    } catch (error) {
      console.error("Failed to save settings:", error)
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <div className="p-6 space-y-6">
        <Skeleton className="h-8 w-48" />
        <Skeleton className="h-32 w-full" />
        <Skeleton className="h-12 w-full" />
      </div>
    )
  }

  return (
    <div className="p-6 space-y-6 overflow-y-auto">
      <div className="text-center">
        <h2 className="text-2xl font-bold mb-2">Business Tools</h2>
        <p className="text-sm text-muted-foreground">Automate and enhance your customer service</p>
      </div>

      {success && (
        <div className="p-3 bg-green-500/10 border border-green-500/20 rounded-lg text-green-600 dark:text-green-400 text-sm">
          {success}
        </div>
      )}

      {/* Away Message */}
      <div className="space-y-4 p-4 bg-secondary/50 rounded-lg border border-border">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <FaCommentDots className="w-5 h-5 text-primary" />
            <div>
              <h3 className="font-semibold">Away Message</h3>
              <p className="text-sm text-muted-foreground">
                Automatically send a message when you're away
              </p>
            </div>
          </div>
          <button
            onClick={() => setAwayMessageEnabled(!awayMessageEnabled)}
            className="text-2xl text-primary"
          >
            {awayMessageEnabled ? <FaToggleOn /> : <FaToggleOff />}
          </button>
        </div>

        {awayMessageEnabled && (
          <div className="space-y-2">
            <label className="text-sm font-medium">Away Message</label>
            <textarea
              value={awayMessage}
              onChange={(e) => setAwayMessage(e.target.value)}
              placeholder="E.g., Thanks for your message! I'm currently away and will respond as soon as possible."
              rows={4}
              className="w-full px-4 py-2 bg-input border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary resize-none"
            />
          </div>
        )}
      </div>

      {/* AI Automation */}
      <div className="space-y-4 p-4 bg-secondary/50 rounded-lg border border-border">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <FaRobot className="w-5 h-5 text-primary" />
            <div>
              <h3 className="font-semibold">AI Automation</h3>
              <p className="text-sm text-muted-foreground">
                Let AI handle common questions automatically
              </p>
            </div>
          </div>
          <button
            onClick={() => setAiAutomationEnabled(!aiAutomationEnabled)}
            className="text-2xl text-primary"
          >
            {aiAutomationEnabled ? <FaToggleOn /> : <FaToggleOff />}
          </button>
        </div>

        {aiAutomationEnabled && (
          <div className="p-3 bg-primary/5 rounded-lg border border-primary/20">
            <p className="text-sm text-muted-foreground">
              AI automation will automatically respond to common customer inquiries based on your business information.
            </p>
          </div>
        )}
      </div>

      <Button
        onClick={handleSave}
        className="w-full"
        disabled={saving}
      >
        {saving ? (
          <>
            <FaSpinner className="w-4 h-4 mr-2 animate-spin" />
            Saving...
          </>
        ) : (
          <>
            <FaSave className="w-4 h-4 mr-2" />
            Save Settings
          </>
        )}
      </Button>
    </div>
  )
}

// Memoize to prevent unnecessary re-renders
export const BusinessToolsTab = memo(BusinessToolsTabComponent)

